* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-weight: 500;
}

body {
  background-color: #f6f0dd;
  height: 100%;
}

a {
  text-decoration: underline;
  color: black;
}

footer {
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
}

.wrapper {
  position: relative;
  text-align: center;
  height: 100vh;
}

.content {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  background-color: rgb(255, 255, 255, 0.95);
  box-shadow: 0 0.75em 0.5em black;
  border-radius: 2em;
  padding: 3em;
}

.name {
  font-weight: 600;
}

.react-icons {
  margin: 0.5em;

  &:hover {
    cursor: pointer;
    transform: scale(1.1);
    transition: all 0.3s;
  }
}

.illustration {
  z-index: -1;
  position: absolute;
  right: 0;
  bottom: 0;
}

@media only screen and (max-width: 960px) {
  .extra {
    display: none;
  }

  .illustration {
    width: 100vw;
  }

  .content {
    top: 50%;
  }
}

@media only screen and (min-width: 960px) {
  .content {
    top: 50%;
  }

  .illustration {
    height: 60vh;
  }
}
